import React from "react";
import Button from "./Button";
import TextArrea from "./TextArrea";

import ConvertContext from './ConvertContext';


const conversionConfig = {
  binary:{
  main : {
    placeholder : "Enter decimal number here",
    function: 'decToBin'
  },  sec : {
    placeholder : "Enter binary number here",
    function: 'binToDec'

  }
},
  hex:{
    main: {
      placeholder : "Enter decimal number here",
      function: 'decToHex'
    },
    sec : {
      placeholder : "Enter hexadecimal number here",
      function: 'hexToDec'
  }


}
}
class Calculator extends React.Component {

  static contextType = ConvertContext;
  state = {
    value: "",
    convertedValue: "",
    conversionType: this.context.converter,
    func: 'main'
  };




  inputReceiver = (event) => {
   const input = (event.target.value);
    this.setState({ value:  input});

  }
  decToBin = () => {
    let val = this.state.value;
    if(val.length!==0){
    val = parseInt(val);
    let bin = '';
    let newVal = val;
    let remainder;
    
    while(newVal!==0){
        remainder = (newVal%2);
        newVal = Math.floor(newVal /= 2);
        //console.log(remainder);
        bin = remainder + bin;
    }
    
    //textArea.innerHTML = '';
    this.setState({convertedValue: bin});
    }
    this.setState({value: ''});

    console.log('CONVERTING DECIMAL TO BINARY!');
    }

    binToDec = () => {
      let textArea = document.getElementById('textarr');
      let val = this.state.value;
      val += '';
      let temp;
      let newVal = 0;
      let length = val.length;
      if(val.length!==0){
      
      for(let i=0;i<length;i++){
      temp = parseInt(val.charAt(i));
      //console.log(temp);
      newVal += temp * Math.pow(2, length-1-i);
      
      
      }
      //textArea.innerHTML = '';
      textArea.value= "";
    this.setState({convertedValue: newVal});
      //console.log(newVal);
      }

      this.setState({value: ''});

      }


  decToHex = () => {
    let val = this.state.value;
    if (val.length !== 0) {
      val = parseInt(val);
      let hex = "";
      let newVal = val;
      let remainder;

      while (newVal !== 0) {
        remainder = newVal % 16;
        newVal = Math.floor((newVal /= 16));
        //console.log(remainder);
        switch (remainder) {
          case 10:
            remainder = "A";
            break;
          case 11:
            remainder = "B";
            break;
          case 12:
            remainder = "C";
            break;
          case 13:
            remainder = "D";
            break;
          case 14:
            remainder = "E";
            break;
          case 15:
            remainder = "F";
            break;
          default:
            break;
        }
        hex = remainder + hex;
      }

      //textArea.innerHTML = '';
      this.setState({ convertedValue: hex });
    }
    this.setState({ value: "" });

    console.log("CONVERTING DECIMAL TO BINARY!");
  };

  hexToDec = () => {
    let textArea = document.getElementById("textarr");
    let val = this.state.value;
    val += "";
    let temp;
    let newVal = 0;
    let length = val.length;
    if (val.length !== 0) {
      for (let i = 0; i < length; i++) {
        temp = (val.charAt(i));
        switch (temp) {
          case 'a':
          case 'A':
              temp = 10;
            break;
            case 'b':
          case 'B':
              temp = 11;
            break;
            case 'c':
          case 'C':
              temp = 12;
            break;
            case 'd':
          case 'D':
              temp = 13;
            break;
            case 'e':
          case 'E':
              temp = 14;
            break;
            case 'f':
          case 'F':
            temp = 15;
            break;
       
          default:
            temp = parseInt(temp);
            break;
        }

        //console.log(temp);
        newVal += temp * Math.pow(16, length - 1 - i);
      }
      //textArea.innerHTML = '';
      textArea.value = "";
      this.setState({ convertedValue: newVal });
      //console.log(newVal);
    }

    this.setState({ value: "" });
  };
    // binToDec = () => {
    //   console.log('CONVERTING BINARY TO DECIMAl!');

    // }

 

  swap = () => {
    if (this.state.func === "main") {
      this.setState({func: 'sec'});
      console.log("Main Conversion");
    }
    else{
      this.setState({func: 'main'})
        console.log("Sec Conversion");
    
    }

    this.setState({value: '', convertedValue: ''});

    //console.log(swapBtn.onclick);
  };

  
  render() {
    return (
      <div className="calc">
        <TextArrea value={this.state.value} onChange={this.inputReceiver} placeholder={conversionConfig[this.state.conversionType][this.state.func].placeholder} readonly={false} />
        <div className="buts">

          <Button id="sub" butType="Submit" onClick={eval('this.'+conversionConfig[this.state.conversionType][this.state.func].function)} />
          <Button id="swap" butType="Swap" onClick={this.swap} />
        </div>
        <TextArrea value={this.state.convertedValue} placeholder="" readonly={true} />
      </div>
    );
  }
}

export default Calculator;
