import React from "react";
import icon from '../icons/icon.svg'
import ConvertContext from './ConvertContext';
import {Link} from 'react-router-dom';
const convertConfig = {
  binary:{
    main: 'Binary',
    sec: 'Hex',
    link: '/hex'
  },
  hex:{
    main: 'Hex',
    sec: 'Binary',
    link: '/'
  }
}
class Header extends React.Component {
   static contextType = ConvertContext;


  render() {
    console.log(this.context)
    return (
      
      <header>
        <div className="navbar navbar-dark bg-dark">
          <div className="d-flex justify-content-start">
            <a className="navbar-brand" href="#">
              {convertConfig[this.context.converter].main} Converter{" "}
              <img className="icon" alt="" width="25" src={icon} />
            </a>
      <ul className="navbar-nav">
        <li className="nav-item pull-right"><Link onClick={(e) => this.context.onConvertChange(e)} className="nav-link"  to={convertConfig[this.context.converter].link}>Switch To {convertConfig[this.context.converter].sec} Converter</Link></li>
      </ul>
            
          </div>
        </div>
      </header>
    );
  }
}

export default Header;
